import React from "react";
import { SettingMenu } from "./settingMenu";
import { useNavigate } from "react-router-dom";
import TitleComponent from "../../../components/Title/TitleComponent";
import logo from "../../../assets/image/Pushbike tangerang logo.png";

const Setting = () => {
  const navigate = useNavigate();

  const handleChangePage = (path) => {
    navigate(path);
  };

  return (
    <div className={"setting"}>
      <TitleComponent title={"Data"} />
      <hr />
      {SettingMenu.map((item, idx) => {
        return (
          <>
            <div
              key={idx}
              className={"setting-item"}
              onClick={() => handleChangePage(item.path)}
            >
              <div className={"icon"}>{React.createElement(item.icon)}</div>
              <div className={"title-menu"}>{item.name}</div>
            </div>
            <div className={"hr"}></div>
          </>
        );
      })}
      <div className={"footer"}>
        <div className={"image-logo"}>
          <img src={logo} alt="logo pbt" />
        </div>
      </div>
      <div className={"website"}>
        <a href="https://pushbiketangerang.com">
          https://pushbiketangerang.com
        </a>
      </div>
      <div className={"app-info"}>
        <p>
          App Version 2.0.0 <br />
          Powered by <a href="https://yantodev.netlify.app/">YantoDev</a>
        </p>
      </div>
    </div>
  );
};

export default Setting;
